import React from "react";

const TitleBanner = ({ title, children }) => {
  return (
    <div style={{ textAlign: "center", marginBottom: "88px" }}>
      <h1
        style={{
          fontSize: "64px",
          fontWeight: "bold",
        }}
      >
        {title}
      </h1>
      {children}
    </div>
  );
};
export default TitleBanner;
